import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			id: -1,
			activity: null,
			author: null,
			attention: 0,
			is_collect: 0,
			is_like: 0,
			is_baoming:0,
			max:1,
			height: 0,
			show: false,
			pl: false,
			comment: {
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			reply: {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			}
		};
	},
	components: { uniPopup },
	onLoad(opt) {
		let that = this;
		uni.getSystemInfo({
			success: function(res) {
				that.height = res.windowHeight / 2 + 'px';
			}
		});
		console.log("活动ID=",opt.id)
		if (opt.id) {
			this.id = opt.id;
			this.getActivity(opt.id);
			this.getCommentList();
		}
	},
	onReachBottom() {
		this.getCommentList();
	},
	onShow() {
		if (this.id > -1) {
			this.getActivity(this.id);
		}
	},
	methods: {
		videoErrorCallback() {},
		getActivity(id) {
			this.$Fetch({
				url: 'activity/getDataById',
				data: { id: id },
				success: res => {
					console.log(res);
					this.activity = res;
					this.author = res.adminUser;
					this.attention = res.attention;
					this.is_collect = res.is_collect;
					this.is_like = res.is_like;
					this.is_baoming=res.is_baoming;
					this.max=res.activity_num - res.people_num
				}
			});
		},
		//关注
		doAttention(id) {
			console.log('关注', id);
			this.$Fetch({
				url:"Attention/add",
				data:{id:id},
				debug:true,
				success:res=>{
					console.log(res)
					this.attention=res;
					let str=res===1?'已关注':'已取消关注';
					uni.showToast({
						title:str,
						icon:"none"
					})
				}
			})
		},
		//收藏
		doCollect(type) {
			this.$Fetch({
				url: 'Collect/add',
				data: {
					collect_type: type,
					id_value: this.id
				},
				success: res => {
					console.log(res);
					this.is_collect = res;
					let str = res === 1 ? '已收藏' : '已取消收藏';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//文章点赞
		doZan(type) {
			this.$Fetch({
				url: 'user_like/add',
				data: {
					like_type: type,
					id_value: this.id
				},
				success: res => {
					console.log(res);
					this.is_like = res;
					let str = res === 1 ? '已点赞' : '已取消点赞';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//获取评论列表
		getCommentList() {
			let data = this.comment;
			if (data.loading) {
				data.loading = false;
				this.comment = data;
				this.$Fetch({
					url: 'Comment/getData',
					data: {
						comment_type: 0,
						id_value: this.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					success: res => {
						let arr = res.lists;
						if (arr.length > 0) {
							data.list = data.list.concat(arr);
							if (arr.length === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						}
						this.comment = data;
					}
				});
			}
		},
		//评论点赞
		addCommentLike(item, index) {
			this.$Fetch({
				url: 'Comment/addCommentLike',
				data: { id: item.id },
				success: res => {
					item.is_like = res;
					if (res === 1) {
						item.like_num++;
					} else {
						item.like_num--;
					}
				}
			});
		},
		closePopup() {
			this.pl = false;
			this.reply = {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			};
		},
		showPopup(item) {
			this.getReply(item);
			this.pl = true;
		},
		getReply(item) {
			let data = this.reply;
			data.comment = item;
			this.reply = data;
			if (data.loading) {
				data.loading = false;
				this.reply = data;
				this.$Fetch({
					url: 'Comment/getComment',
					data: {
						id: item.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					debug: true,
					success: res => {
						let len = res.length;
						if (len > 0) {
							data.list = data.list.concat(res);
							if (len === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						}
						this.reply = data;
						this.pl = true;
					}
				});
			}
		},
		onSubmit(e) {
			console.log('提交数据：', e.detail.value);
			this.$Fetch({
				url: 'activity_list/add',
				data: e.detail.value,
				success: res => {
					console.log(res);
					uni.navigateTo({
						url: '../success/success?id='+res
					});
				}
			});
		}
	}
};
